import { NextRequest, NextResponse } from "next/server";
import dbConnect from "../../../../lib/dbConnect";
import SourceSetting from "../../../../models/SourceSetting";
import { verifyToken } from "../../../../lib/auth";

// Helper function to verify authentication
async function verifyAuth(request: NextRequest) {
    const token = request.cookies.get("token")?.value;
    if (!token) return null;

    const decoded = verifyToken(token);
    if (!decoded) return null;

    return decoded;
}

// GET - L·∫•y m·ªôt source setting theo ID
export async function GET(
    request: NextRequest,
    { params }: { params: Promise<{ id: string }> },
) {
    try {
        const auth = await verifyAuth(request);
        if (!auth) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng ƒë∆∞·ª£c ph√©p truy c·∫≠p" },
                { status: 401 },
            );
        }

        await dbConnect();
        const { id } = await params;

        const sourceSetting = await SourceSetting.findById(id);

        if (!sourceSetting) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng t√¨m th·∫•y ngu·ªìn kh√°ch h√†ng" },
                { status: 404 },
            );
        }

        return NextResponse.json({
            success: true,
            data: sourceSetting,
        });
    } catch (error) {
        console.error("Error fetching source setting:", error);
        return NextResponse.json(
            { success: false, message: "C√≥ l·ªói x·∫£y ra. Vui l√≤ng th·ª≠ l·∫°i." },
            { status: 500 },
        );
    }
}

// PUT - C·∫≠p nh·∫≠t source setting
export async function PUT(
    request: NextRequest,
    { params }: { params: Promise<{ id: string }> },
) {
    try {
        const auth = await verifyAuth(request);
        if (!auth) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng ƒë∆∞·ª£c ph√©p truy c·∫≠p" },
                { status: 401 },
            );
        }

        await dbConnect();
        const { id } = await params;
        const body = await request.json();

        const sourceSetting = await SourceSetting.findByIdAndUpdate(
            id,
            { ...body, updatedAt: new Date() },
            { new: true, runValidators: true },
        );

        if (!sourceSetting) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng t√¨m th·∫•y ngu·ªìn kh√°ch h√†ng" },
                { status: 404 },
            );
        }

        return NextResponse.json({
            success: true,
            message: "C·∫≠p nh·∫≠t th√†nh c√¥ng",
            data: sourceSetting,
        });
    } catch (error) {
        console.error("Error updating source setting:", error);
        return NextResponse.json(
            { success: false, message: "C√≥ l·ªói x·∫£y ra. Vui l√≤ng th·ª≠ l·∫°i." },
            { status: 500 },
        );
    }
}

